import Link from "next/link";

const Navbar = () => {
	return (
		<div>
			<nav>
				<Link href="/">
					<a>Home</a>
				</Link>
				<Link href="/about">
					<a>About</a>
				</Link>
			</nav>
			<style jsx>{`
				nav {
					position: flex;
					flex-direction: row;
					width: 100%;
					height: auto;
					justify-content: flex-start;
					background-color: red;
				}
				a {
					margin: 1rem;
				}
			`}</style>
		</div>
	);
};

export default Navbar;
